import Swal from 'sweetalert2';

const mensaje = (tipo, titulo, texto, liga) => {
    Swal.fire({
        icon: tipo,
        title: titulo,
        text: texto,
        footer: liga
    });
}

const getTextInputById = (id) => {
    const element = document.getElementById(id);
    return element ? element.value.trim() : "";
}

const validaLogin = () => {
    let user = getTextInputById("user").trim();
    let pass = getTextInputById("pass").trim();

    if (user === '') {
        mensaje('error', 'Usuario Vaco', 'El campo Usuario no puede ir vaco', 'Favor de llenar el campo solicitado');
        return false;
    } else if (pass === '') {
        mensaje('error', 'Contrasea Vaca', 'El campo Contrasea no puede ir vaco', 'Favor de llenar el campo solicitado');
        return false;
    }

    return true;
}

export default validaLogin;
