import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import validaLogin from '../js/validaLogin'; // Importa la funcin de validacin
import Swal from 'sweetalert2';
import styles from '../css/estiloLogin.module.css'

const Login = () => {
    const [user, setUser] = useState('');
    const [pass, setPass] = useState('');
    const navigate = useNavigate();

    const handleLogin = async (e) => {
        e.preventDefault();

        // Utiliza la funcin de validacin antes de intentar iniciar sesin
        if (!validaLogin()) {
            return; // La validacin fall, no contines con el inicio de sesin
        }

        try {
            // Realiza una solicitud GET al backend para verificar las credenciales
            const response = await axios.get(`http://localhost:8000/login/${user}`, {
                params: { pass }, // Enva la contrasea como parmetro de consulta
            });

            if (response.data.message === 'Inicio de sesin exitoso') {
                // Usuario autenticado exitosamente, redirige a la pgina de administrador
                navigate('/turnosLogeado'); // Cambia '/admin' a la URL deseada.
            } else {
                // Las credenciales son incorrectas, muestra un mensaje de error con SweetAlert2
                Swal.fire({
                    icon: 'error',
                    title: 'Credenciales Incorrectas',
                    text: 'El usuario y la contrasea proporcionados no son correctos',
                });
                console.log('Credenciales incorrectas');
            }
        } catch (error) {
            // Error al realizar la solicitud (puede ser un error de red o del servidor)
            Swal.fire({
                icon: 'error',
                title: 'Error al iniciar sesin',
                text: 'Se ha producido un error al intentar iniciar sesin. Por favor, intntelo de nuevo ms tarde.',
            });
            console.error('Error al iniciar sesin:', error);
        }
    };

    return (
        <div className={styles.loginContainer}>
            <h2>Iniciar Sesin</h2>
            <form onSubmit={handleLogin}>
                <div>
                    <label>Usuario:</label>
                    <input
                        type="text"
                        value={user}
                        onChange={(e) => setUser(e.target.value)}
                        id="user" // Asigna un ID al campo de usuario
                    />
                </div>
                <div>
                    <label>Contrasea:</label>
                    <input
                        type="password"
                        value={pass}
                        onChange={(e) => setPass(e.target.value)}
                        id="pass" // Asigna un ID al campo de contrasea
                    />
                </div>
                <button type="submit">Iniciar Sesin</button>
                <button type='button' className='btn btn-secondary' onClick={() => navigate('/')}>Cancelar</button>
            </form>
        </div>
    );
};

export default Login;
